import React, { useState } from "react";
import { Layout, Icon, Avatar, Badge, Drawer } from "antd";
import { Link } from "react-router-dom";
import "./style.css";
const { Header } = Layout;

const Headers = () => {
  const [state, setState] = useState({
    visibleNotice: false,
    visibleSetting: false,
    visibleHelp: false,
    visibleUser: false
  });

  const showDrawerNotice = () => {
    setState({
      ...state,
      visibleNotice: true
    });
  };

  const onCloseNotice = () => {
    setState({
      ...state,
      visibleNotice: false
    });
  };
  const showDrawerSetting = () => {
    setState({
      ...state,
      visibleSetting: true
    });
  };

  const onCloseSetting = () => {
    setState({
      ...state,
      visibleSetting: false
    });
  };
  const showDrawerHelp = () => {
    setState({
      ...state,
      visibleHelp: true
    });
  };

  const onCloseHelp = () => {
    setState({
      ...state,
      visibleHelp: false
    });
  };
  const showDrawerUser = () => {
    setState({
      ...state,
      visibleUser: true
    });
  };

  const onCloseUser = () => {
    setState({
      ...state,
      visibleUser: false
    });
  };

  return (
    <Header className="header" style={{ lineHeight: "55px", height: "55px" }}>
      <div className="logo" />
      <div className="header-left">
        <div className="header-left-img">
          <img
            src="data:image/jpeg;base64,"
            alt="icon interface"
          />
        </div>
        <div className="header-left-text">
          <div className="header-left-text-large">
            <span>KeHoachA</span>
            <Icon type="star" />
          </div>
          <div className="header-left-text-small">
            <span>ThucThi</span>
          </div>
        </div>
      </div>
      <div className="header-center">
        <div className="header-center-menu">
          <div className="header-center-item">
            <Link to="">Bảng</Link>
          </div>
          <div className="header-center-item">
            <Link to="">Biểu đồ</Link>
          </div>
          <div className="header-center-item">
            <Link to="">Lịch biểu</Link>
          </div>
          <div className="header-center-item">
            <Link to="/process">Kiểm tra quy trình</Link>
          </div>
          <div className="header-center-item">
            <Link to="/">Giao việc cho nhân viên</Link>
          </div>
        </div>
      </div>
      <div className="header-right">
        <div className="header-tool">
          <div className="header-tool-item notice">
            <Icon type="bell" onClick={showDrawerNotice} />
            <Badge count={199} overflowCount={99}>
              <Link to="" className="head-example" />
            </Badge>
            <Drawer
              title="Thông báo"
              width={300}
              onClose={onCloseNotice}
              visible={state.visibleNotice}
            >
              <div />
            </Drawer>
          </div>
          <div className="header-tool-item setting">
            <Icon type="setting" onClick={showDrawerSetting} />
            <Drawer
              title="Cài đặt"
              width={300}
              onClose={onCloseSetting}
              visible={state.visibleSetting}
            >
              <div />
            </Drawer>
          </div>
          <div className="header-tool-item help">
            <Icon
              type="question-circle"
              theme="twoTone"
              onClick={showDrawerHelp}
            />
            <Drawer
              title="Trợ giúp"
              width={300}
              onClose={onCloseHelp}
              visible={state.visibleHelp}
            >
              <div />
            </Drawer>
          </div>
          <div className="header-right-account header-tool-item">
            <Avatar
              style={{ backgroundColor: "#87d068" }}
              icon="user"
              onClick={showDrawerUser}
            />
            <span onClick={showDrawerUser}>Tên người dùng</span>
            <Drawer
              title="Tài khoản của bạn"
              width={300}
              onClose={onCloseUser}
              visible={state.visibleUser}
            >
              <div className="drawer-acount">
                <div className="drawer-account-avatar">
                  <div className="drawer-account-inner">
                    <div className="drawer-account-avatar">
                      <Avatar
                        size={80}
                        style={{ backgroundColor: "#87d068" }}
                        icon="user"
                      />
                    </div>
                    <div className="drawer-account-info">
                      <span className="drawer-account-username">
                        Người dùng
                      </span>
                      <span className="drawer-account-email">
                        nguoidung@gmail.com
                      </span>
                      <Link to="">Tài khoản</Link>
                      <Link to="">Đăng xuất</Link>
                    </div>
                  </div>
                </div>
              </div>
            </Drawer>
          </div>
        </div>
      </div>
    </Header>
  );
};

export default Headers;
